.backdrop {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100vh;
  z-index: 10;
  opacity: 0.9;
}

@media (max-width: 512px) {
  .modal {
    width: 80%;
  }
}

@media (min-width: 513px) {
  .modal {
    width: 30rem;
  }
}
.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 15rem;
  background-color: black;
  border: 5px solid green;
  border-radius: 10px;
  display: flex;
}

.modaltext {
  color: white;
  margin: auto;
}

.modalbtns {
  position: absolute;
  right: 30px;
  bottom: 20px;
}

.modalbtns > * {
  margin: 10px;
}

.redBorder {
  border-color: red;
}

.greenBorder {
  border-color: green;
}
